@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,400;0,700;1,400&display=swap');
@import url('../normalize.css');

$panels: 'power', 'velocity', 'weight';
$languages: 'HU', 'EN';
$measurements: 'metric', 'imperial';

html {
    min-height: 100%;
    background-image: linear-gradient(180deg, rgba(255,255,255,.4) 0%, rgba(245, 243, 244,.4) 100%);
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: 50% 50%;
}
body {
}

.FpsConverter {
    font-family: 'Rubik', sans-serif;
    font-size: 20px;
}

.FpsConverterHeader {
    box-sizing: border-box;
    background-image: linear-gradient(180deg, rgba(245,243,244,1) 0%, rgba(177,167,166,1) 100%);
    background-position: 50% 50%;
    background-repeat: no-repeat;
    box-shadow: 0 0 4px 4px rgba(177, 167, 166, .5);

    @media (orientation: portrait) {
        height: 6rem;
        background-size: 100% 100%;
    }
    @media (orientation: landscape) {
        height: 5rem;
        background-size: 100% 100%;
    }
}
.FpsConverterHeaderContent {
    box-sizing: border-box;
    margin: auto;
    max-width: 768px;
    display: flex;
    flex-direction: row;
    padding: 0 24px;
    align-items: center;

    background-image: url(../img/header.svg);
    background-position: center right 24px;
    background-repeat: no-repeat;

    @media (orientation: portrait) {
        height: 6rem;
        background-size: 50% auto;
    }
    @media (orientation: landscape) {
        height: 5rem;
        background-size: auto 95%;
    }
}
.FpsConverterTitle {
    flex-basis: calc(50% - 1rem);
    font-size: 1.25rem;
    padding: 0 4px;
    color: #fff;

    @media (orientation: landscape) {
        font-size: 1.5rem;
    }

    SPAN {
        box-decoration-break: clone;
        background-color: rgba(229, 56, 59,1);
        box-shadow: -4px 0 0 rgba(229, 56, 59,1), 4px 0 0 rgba(229, 56, 59,1);
    }
}

.FpsConverterContent {
    margin: auto;
    max-width: 768px;
}

.Panel {
    display: flex;
    margin: 16px 0;
}

.CalculatorPanel {
    display: block;
    margin: 24px;
    padding: 16px;
    border-radius: 8px;
    background-color: rgba(211,211,211,1);
    transition: background-color 400ms;

    @each $panel in $panels {
        .FpsConverter[data-panel="#{$panel}"] &[data-panel="#{$panel}"] {
            background-color: rgba(177,167,166,1);
        }
    }
}
.CalculatorPanelForm {
    display: grid;
    gap: 1rem;

    @media (orientation: portrait) {
        grid-template-columns: 1fr;
    }
    @media (orientation: landscape) {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
}
.CalculatorPanelTitle {
    font-weight: 700;
    margin-bottom: 8px;

    SPAN {
        display: inline-block;
        text-decoration: underline;
        text-decoration-color: #E5383B;
        text-decoration-thickness: 2px;
    }
}
.CalculatorPanelSubtitle {
    font-size: 75%;
    font-style: italic;
    margin-bottom: 8px;
}
.CalculatorPanelBlock {
    display: grid;
    align-items: center;
    justify-content: center;
    gap: .25rem;

    @media (orientation: portrait) {
        grid-template-columns: 1fr;
    }
    @media (orientation: landscape) {
        grid-template-columns: 1fr auto;
    }

    &[data-type=output] {
        background: rgba(245,243,244,1);
        grid-template-columns: auto auto auto;
        box-shadow: inset 0 0 8px 4px rgb(127,127,127,.4);
        border-radius: 8px;
        padding: 8px;

        @media (orientation: landscape) {
            grid-column-start: 1;
            grid-column-end: 3;
        }
    }
}
.CalculatorPanelLabel {
    @media (orientation: landscape) {
        .CalculatorPanelBlock:not([data-type=output]) & {
            grid-column-start: 1;
            grid-column-end: 3;
        }
    }
}
.CalculatorPanelInput {
}
.CalculatorPanelUnit {
    display: block;
    flex-basis: 100%;
    font-size: 75%;
    font-style: italic;
    color: #444;
}
.CalculatorPanelOutput {
    font-size: 125%;
    font-weight: bold;

    .CalculatorPanelBlock[data-type=output] &:before {
        content: '= ';
        color: rgba(0,0,0,.4);
    }
}

.SettingsPanel {
    background: #fff;
    box-sizing: border-box;
    padding: 16px;
    box-shadow: -2px 0 5px 0 rgba(0,0,0,.2);    
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    display: flex;
    gap: 1vw;
    flex-direction: column;
    transition: all 400ms cubic-bezier(0.69, -0.43, 0.63, 1.45);
    transform: translateY(100%);

    &[data-opened=true] {
        transform: translateY(0);
    }

    &:after {
        content: '';
        position: absolute;
        bottom: -20px; left: 0;
        width: 100%;
        height: 20px;
        background: inherit;
    }
}
.SettingsPanelToggler {
    position: absolute;
    width: 40px; height: 40px;
    top: -60px; right: 24px;
    border-radius: 8px;
    background-color: #ccc;
    background-image: url(../img/settings-icon-cog.svg);
    background-repeat: no-repeat;
    background-size: 32px 32px;
    background-position: 50% 50%;
    transition: all 400ms cubic-bezier(0.69, -0.43, 0.63, 1.45);

    .SettingsPanel[data-opened=true] & {
        top: -20px;
        background-color: transparent;
        background-image: url(../img/settings-icon-close.svg);
    }
}

.LanguageSelector, .MeasurementSelector {
    display: flex;
    flex-direction: row;
    gap: 1vw;
}
.LanguageSelectorItem, .MeasurementSelectorItem {
    flex-basis: 50%;
    height: 5vh;
    line-height: 5vh;
    text-align: center;
    background: #ddd;
    border-radius: 5px;
}
.LanguageSelectorItem {
    @each $language in $languages {
        .LanguageSelector[data-lang="#{$language}"] &[data-lang="#{$language}"] {
            font-weight: bold;
        }
    }
}
.MeasurementSelectorItem {
    @each $measurement in $measurements {
        .MeasurementSelector[data-measurement="#{$measurement}"] &[data-measurement="#{$measurement}"] {
            font-weight: bold;
        }
    }
}
