@import url("https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,400;0,700;1,400&display=swap");
@import url("../normalize.css");
html {
  min-height: 100%;
  background-image: linear-gradient(180deg, rgba(255, 255, 255, 0.4) 0%, rgba(245, 243, 244, 0.4) 100%);
  background-repeat: no-repeat;
  background-size: auto 100%;
  background-position: 50% 50%;
}

.FpsConverter {
  font-family: "Rubik", sans-serif;
  font-size: 20px;
}

.FpsConverterHeader {
  box-sizing: border-box;
  background-image: linear-gradient(180deg, #f5f3f4 0%, #b1a7a6 100%);
  background-position: 50% 50%;
  background-repeat: no-repeat;
  box-shadow: 0 0 4px 4px rgba(177, 167, 166, 0.5);
}
@media (orientation: portrait) {
  .FpsConverterHeader {
    height: 6rem;
    background-size: 100% 100%;
  }
}
@media (orientation: landscape) {
  .FpsConverterHeader {
    height: 5rem;
    background-size: 100% 100%;
  }
}

.FpsConverterHeaderContent {
  box-sizing: border-box;
  margin: auto;
  max-width: 768px;
  display: flex;
  flex-direction: row;
  padding: 0 24px;
  align-items: center;
  background-image: url(../img/header.svg);
  background-position: center right 24px;
  background-repeat: no-repeat;
}
@media (orientation: portrait) {
  .FpsConverterHeaderContent {
    height: 6rem;
    background-size: 50% auto;
  }
}
@media (orientation: landscape) {
  .FpsConverterHeaderContent {
    height: 5rem;
    background-size: auto 95%;
  }
}

.FpsConverterTitle {
  flex-basis: calc(50% - 1rem);
  font-size: 1.25rem;
  padding: 0 4px;
  color: #fff;
}
@media (orientation: landscape) {
  .FpsConverterTitle {
    font-size: 1.5rem;
  }
}
.FpsConverterTitle SPAN {
  box-decoration-break: clone;
  background-color: #e5383b;
  box-shadow: -4px 0 0 #e5383b, 4px 0 0 #e5383b;
}

.FpsConverterContent {
  margin: auto;
  max-width: 768px;
}

.Panel {
  display: flex;
  margin: 16px 0;
}

.CalculatorPanel {
  display: block;
  margin: 24px;
  padding: 16px;
  border-radius: 8px;
  background-color: lightgray;
  transition: background-color 400ms;
}
.FpsConverter[data-panel=power] .CalculatorPanel[data-panel=power] {
  background-color: #b1a7a6;
}
.FpsConverter[data-panel=velocity] .CalculatorPanel[data-panel=velocity] {
  background-color: #b1a7a6;
}
.FpsConverter[data-panel=weight] .CalculatorPanel[data-panel=weight] {
  background-color: #b1a7a6;
}

.CalculatorPanelForm {
  display: grid;
  gap: 1rem;
}
@media (orientation: portrait) {
  .CalculatorPanelForm {
    grid-template-columns: 1fr;
  }
}
@media (orientation: landscape) {
  .CalculatorPanelForm {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }
}

.CalculatorPanelTitle {
  font-weight: 700;
  margin-bottom: 8px;
}
.CalculatorPanelTitle SPAN {
  display: inline-block;
  text-decoration: underline;
  text-decoration-color: #E5383B;
  text-decoration-thickness: 2px;
}

.CalculatorPanelSubtitle {
  font-size: 75%;
  font-style: italic;
  margin-bottom: 8px;
}

.CalculatorPanelBlock {
  display: grid;
  align-items: center;
  justify-content: center;
  gap: 0.25rem;
}
@media (orientation: portrait) {
  .CalculatorPanelBlock {
    grid-template-columns: 1fr;
  }
}
@media (orientation: landscape) {
  .CalculatorPanelBlock {
    grid-template-columns: 1fr auto;
  }
}
.CalculatorPanelBlock[data-type=output] {
  background: #f5f3f4;
  grid-template-columns: auto auto auto;
  box-shadow: inset 0 0 8px 4px rgba(127, 127, 127, 0.4);
  border-radius: 8px;
  padding: 8px;
}
@media (orientation: landscape) {
  .CalculatorPanelBlock[data-type=output] {
    grid-column-start: 1;
    grid-column-end: 3;
  }
}

@media (orientation: landscape) {
  .CalculatorPanelBlock:not([data-type=output]) .CalculatorPanelLabel {
    grid-column-start: 1;
    grid-column-end: 3;
  }
}

.CalculatorPanelUnit {
  display: block;
  flex-basis: 100%;
  font-size: 75%;
  font-style: italic;
  color: #444;
}

.CalculatorPanelOutput {
  font-size: 125%;
  font-weight: bold;
}
.CalculatorPanelBlock[data-type=output] .CalculatorPanelOutput:before {
  content: "= ";
  color: rgba(0, 0, 0, 0.4);
}

.SettingsPanel {
  background: #fff;
  box-sizing: border-box;
  padding: 16px;
  box-shadow: -2px 0 5px 0 rgba(0, 0, 0, 0.2);
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100vw;
  display: flex;
  gap: 1vw;
  flex-direction: column;
  transition: all 400ms cubic-bezier(0.69, -0.43, 0.63, 1.45);
  transform: translateY(100%);
}
.SettingsPanel[data-opened=true] {
  transform: translateY(0);
}
.SettingsPanel:after {
  content: "";
  position: absolute;
  bottom: -20px;
  left: 0;
  width: 100%;
  height: 20px;
  background: inherit;
}

.SettingsPanelToggler {
  position: absolute;
  width: 40px;
  height: 40px;
  top: -60px;
  right: 24px;
  border-radius: 8px;
  background-color: #ccc;
  background-image: url(../img/settings-icon-cog.svg);
  background-repeat: no-repeat;
  background-size: 32px 32px;
  background-position: 50% 50%;
  transition: all 400ms cubic-bezier(0.69, -0.43, 0.63, 1.45);
}
.SettingsPanel[data-opened=true] .SettingsPanelToggler {
  top: -20px;
  background-color: transparent;
  background-image: url(../img/settings-icon-close.svg);
}

.LanguageSelector, .MeasurementSelector {
  display: flex;
  flex-direction: row;
  gap: 1vw;
}

.LanguageSelectorItem, .MeasurementSelectorItem {
  flex-basis: 50%;
  height: 5vh;
  line-height: 5vh;
  text-align: center;
  background: #ddd;
  border-radius: 5px;
}

.LanguageSelector[data-lang=HU] .LanguageSelectorItem[data-lang=HU] {
  font-weight: bold;
}
.LanguageSelector[data-lang=EN] .LanguageSelectorItem[data-lang=EN] {
  font-weight: bold;
}

.MeasurementSelector[data-measurement=metric] .MeasurementSelectorItem[data-measurement=metric] {
  font-weight: bold;
}
.MeasurementSelector[data-measurement=imperial] .MeasurementSelectorItem[data-measurement=imperial] {
  font-weight: bold;
}

